/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
/* ~~~~~~~~~~ Content ~~~~~~~~~~ */
/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

body#tinymce,
.content {

    /* ~~~~~~~~~~ First element fix ~~~~~~~~~~ */

    .first-element-fix {
        & + * {
            margin-top: 0;
        }
    }


    /* ~~~~~~~~~~ Headlines ~~~~~~~~~~ */

    h2,
    h3,
    h4,
    h5,
    h6 {
        margin-top: 40px;
    }


    /* ~~~~~~~~~~ Text elements ~~~~~~~~~~ */

    > p {
        @extend .element-small-margin-top;
    }

    a:not([class*='-button']) {
        &:hover,
        &:focus {
            border-color: $primary-color;
        }
    }


    /* ~~~~~~~~~~ ul, ol lists ~~~~~~~~~~ */

    ul {
        list-style-type: disc;
    }

    ol {
        list-style-type: decimal;
    }

    ul,
    ol {
        @extend .element-small-margin-top;
        list-style-position: outside;
        margin-left: 25px;

        ul,
        ol {
            list-style-position: outside;
            margin: 0 0 15px 25px;

            @include media-breakpoint-up(sm) {
                margin-bottom: 20px;
            }

            @include media-breakpoint-up(md) {
                margin-bottom: 25px;
            }
        }

        ul {
            list-style-type: circle;
        }

        ol {
            list-style-type: lower-latin;
        }

        li {
            margin-top: 10px;
        }
    }


    /* ~~~~~~~~~~ Images ~~~~~~~~~~ */

    img,
    .wp-caption {
        width: auto;
        height: auto;
        max-width: 100%;
        margin-top: 25px;
        display: block;

        &.alignleft {
            float: left;
            width: auto;
            max-width: 100%;
            margin: 0 25px 15px 0;
        }

         &.alignright {
            float: right;
            width: auto;
            max-width: 100%;
            margin: 0 0 15px 25px;
        }

        &.aligncenter {
            width: auto;
            max-width: 100%;

            @include media-breakpoint-up(sm) {
                margin-left: auto;
                margin-right: auto;
            }
        }

        .wp-caption-text {
            @include fluid-type(13px, 14px);
            margin-top: 10px;
            text-align: center;
            font-style: italic !important;
        }
    }


    /* ~~~~~~~~~~ iFrame ~~~~~~~~~~ */

    iframe {
        width: 100%;
        height: 200px;
        margin-top: 25px;

        @include media-breakpoint-up(sm) {
            height: 250px;
        }

        @include media-breakpoint-up(md) {
            height: 308px;
        }

        @include media-breakpoint-up(lg) {
            height: 390px;
        }

        @include media-breakpoint-up(xl) {
            height: 410px;
        }
    }


    /* ~~~~~~~~~~ Gallery ~~~~~~~~~~ */

    .gallery {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: 40px !important;

        &.gallery-columns-2 {
            .gallery-item {
                flex-basis: 49%;
            }
        }

        &.gallery-columns-3 {
            .gallery-item {
                flex-basis: 32%;
            }
        }

        &.gallery-columns-4 {
            .gallery-item {
                flex-basis: 24%;
            }
        }

        &.gallery-columns-5 {
            .gallery-item {
                flex-basis: 19%;
            }
        }

        &.gallery-columns-6 {
            .gallery-item {
                flex-basis: 16%;
            }
        }

        &.gallery-columns-7 {
            .gallery-item {
                flex-basis: 13.5%;
            }
        }

        &.gallery-columns-8 {
            .gallery-item {
                flex-basis: 12%;
            }
        }

        &.gallery-columns-9 {
            .gallery-item {
                flex-basis: 10.5%;
            }
        }

        .gallery-item {
            img {
                border: none !important;
                margin-top: 0;
            }
        }
    }


    /* ~~~~~~~~~~ Preformatted ~~~~~~~~~~ */

    pre {
        @include fluid-type(15px, 16px);
        margin-top: 25px;
        background: #3A3A3A;
        border-radius: 3px;
        padding: 25px;
        color: #fff;
        white-space: pre-wrap;
        white-space: -moz-pre-wrap;
        white-space: -pre-wrap;
        white-space: -o-pre-wrap;
        word-wrap: break-word;
        display: block;
    }


    /* ~~~~~~~~~ Blockquote ~~~~~~~~~~ */

    blockquote {
        margin-top: 25px;
        border-left: 2px solid $primary-color;
        padding-left: 15px;

        @include media-breakpoint-up(sm) {
            border-left-width: 3px;
            padding-left: 20px;
        }

        @include media-breakpoint-up(md) {
            border-left-width: 4px;
            padding-left: 25px;
        }

        @include media-breakpoint-up(lg) {
            border-left-width: 5px;
        }
    }


    /* ~~~~~~~~~~ Table ~~~~~~~~~~ */

    table {
        border: 1px solid #E5E5E5;
        margin-top: 40px;

        tr {
            border-bottom: 1px solid #E5E5E5;

            &:last-child {
                border: 0;
            }
        }

        td {
            border-right: 1px solid #E5E5E5;
            padding: 15px;

            &:last-child {
                border: 0;
            }
        }
    }

}